.services-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    padding-bottom: 2rem;
}
.title-1{
    font-size: 1.4rem;
    font-weight: 600;
    color: var(--main-color);
    margin-bottom: 1rem;
}
.title-2{
    font-size: 2rem;
    font-weight: 600;
}
.title-3{
    font-size: 1.2rem;
    color: gray;
    font-weight: 400;
    margin-top: 1.5rem;
}
.services-all{
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
}
.services-item{
    width: 22%;
    border-radius: 1.5rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    cursor: pointer;
}
.services-img{
    width: 100%;
    border-radius: 1.5rem;
}
.services-name{
    font-size: 1.2rem;
    font-weight: 600;
}
