
iframe{
    width: 100%;
    height: 70vh;
    /* filter: invert(100%); */
}

.title{
    padding-top: 5rem;
    font-size: 4.5rem;
    font-weight: 600;
    width: fit-content;
}

.contact-main{
    display: flex;
}
.contact-info{
    flex: 1;
    padding-bottom: 6rem ;
}
h2{
    font-size: 2rem;
    font-weight: 600;
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.contact-item{
    font-size: 1.8rem;
    font-weight: 300;
    margin-bottom: 1rem;
    cursor: pointer;
}
.contact-data:hover{
    color: var(--main-color);
}
.contact-address-icon{
    color:skyblue;
    margin-right: 1rem;
}
.contact-phone-icon{
    color: red;
    margin-right: 1rem;
}
.contact-mail-icon{
    color: blue;
    margin-right: 1rem;
}

.contact-form{
    flex: 1;
    position: relative;
}
.form{
    width: 30vw;
    min-height: 80vh;
    border: 5px solid var(--main-color);
    background-color: white;
    border-radius: 1rem;
    padding: 2rem 2rem;
    position: absolute;
    left: 2rem;
}
hr{
    height: 0;
    border: 1px solid rgb(8, 8, 245);
}
h3{
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
}
.form > span{
    font-size: 1.2rem;
    font-weight: 300;
}
form{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
}
input, textarea{
    margin-top: 0.5rem;
    border: none;
    outline: none;
}
.form-btn{
    background-color: var(--main-color);
    color: white;
    align-self: flex-start;
    padding: 0.5rem 1rem;
    font-size: 1.2rem;
    border-radius: 1rem;
    font-weight: 600;
}
.form-btn:hover{
    background-color: var(--hover-color);
}